import React,{Component} from 'react'
import Link from 'gatsby-link'
import '../assets/styles/index.css'
import { InputGroupAddon,InputGroup, Button, Row, Col, Input } from 'reactstrap'
import createHistory from "history/createBrowserHistory"
import MButton from 'material-ui/Button'
import Card from 'material-ui/Card'
import LazyLoad from 'react-lazyload'
import { OverPack } from 'rc-scroll-anim'
import TweenOne from 'rc-tween-one'
import QueueAnim from 'rc-queue-anim'

class IndexPage extends Component{
		state={
				tel:'',
				code:'',
				sended: false
		}
		componentWillMount(){
				let query = this.props.location.search.slice(1)
				console.log(query)
		}
		telInput = async(e) =>{
				await this.setState({tel: e.target.value})
				console.log(this.state.tel)
		}
		codeInput = async(e) =>{
				await this.setState({code: e.target.value})
				console.log(this.state.code)
		}
		sendMsg = async() =>{
				let str = "phone=+86" + this.state.tel
				let res = await fetch('http://ipnodes.io/api/send_phone_code', {
						method: 'POST',
						body:  str,
						headers: {
								"Content-Type": "application/x-www-form-urlencoded"
						}
				})
				let resJSON = JSON.parse(res._bodyText)
				alert(resJSON.message)
				if(res.JSON.message='!'){
						this.setState({sended: true})
				}
		}
		verifyMsg = async() =>{
				let str = "phone=+86" + this.state.tel + "&code=" + this.state.code
				let res = await fetch('http://ipnodes.io/api/send_phone_code', {
						method: 'POST',
						body:  str,
						headers: {
								"Access-Control-Allow-Origin":"*"
						}
				})
				let resJSON = JSON.parse(res._bodyText)
				alert(resJSON.message)
		}
		render(){
				const history = createHistory()				
				return(
						<div id="page-ctn">
								<LazyLoad height={200}>
										<section className="is1" style={{height:'100vh',width:'100%'}}>
												<h1> IPNodes </h1>
												<text className="subtitle" style={{paddingBottom:'2rem'}}>
														IPFS, ,  | 2.3G, 4T, 4G</text>
												<div className="machine" />						
												<div id="grid1">
														<Input onChange={this.telInput}		style={{marginTop: '2rem'}}
														className="tel" placeholder="" />
														<InputGroup style={{marginTop:'1rem'}}>
																<Input placeholder=""  onChange={this.codeInput} />
																<InputGroupAddon addonType="append">
																		<Button color="secondary"
																				disabled={this.state.sended}
																				onClick={this.sendMsg}></Button>
																</InputGroupAddon>
														</InputGroup>
														<Button style={{margin:'1rem 0', width:'100%'}}
																disabled={!this.state.sended} color="danger">
																</Button>
														<h1 id="price">
																<s className="raw">3999/</s><br/> 2999/</h1>
												</div>
										</section></LazyLoad>
								<LazyLoad height={200} ><section className="is2">
										<h1></h1>
										<h6></h6>
										<Row className="grid">
												<Col md={3} xs={12} className="g2c"><Row>
														<Col md={12} xs={4} ><div className="g2i i1"/></Col>
														<Col>
																<Row><Col className="g2t">IPFS</Col></Row>
																<Row><Col className="g2p"><br/></Col></Row>
														</Col>
												</Row></Col>
												<Col md={3} xs={12} className="g2c"><Row>
														<Col md={12} xs={4} ><div className="g2i i2"/></Col>
														<Col md={12}>
																<Row><Col className="g2t"></Col></Row>
																<Row><Col className="g2p">OS<br/>IPN</Col></Row>
														</Col>
												</Row></Col>
												<Col md={3} xs={12} className="g2c"><Row>
														<Col md={12} xs={4} ><div className="g2i i3"/></Col>
														<Col>
																<Row><Col className="g2t"></Col></Row>
																<Row><Col className="g2p"><br/></Col></Row>
														</Col>
												</Row></Col>
												<Col md={3} xs={12} className="g2c"><Row>
														<Col md={12} xs={4} ><div className="g2i i4"/></Col>
														<Col>
																<Row><Col className="g2t">CDN</Col></Row>
																<Row><Col className="g2p"><br/></Col></Row>
														</Col>
												</Row></Col>
										</Row>
								</section></LazyLoad>
								<LazyLoad height={200}><section className="is3">
										<h1> + </h1>
										<h6>,/,</h6>
										<Row className="grid grid2">
												<Col md={2} xs={0}/>
												<Col md={5} xs={12} className="g3iw"><div className="g3i" /></Col>
												<Col >
														<Col className="g2c">
																<Row className="g2t"></Row>
																<Row className="g3p ">
																		<br/>/
																</Row>
														</Col>
														<Col className="g2c">
																<Row className="g2t">IOS/, </Row>
																<Row className="g3p">
																		IOS/APP<br/>   
																</Row>
														</Col>
														<Col className="g2c">
																<Row className="g2t"></Row>
																<Row className="g3p">
																		<br/> 
																</Row>
														</Col>
												</Col>
										</Row>
								</section></LazyLoad>
								<LazyLoad height={200}><section className="isb">
										<h1></h1>
										<h6>DApp, , </h6>
										<Row className="isbgrid">
												<Col md={2} xs={0} />
												<Col md={4} xs={12} className="isbiw">
														<Card><div className="isbi isbi1"/></Card>
														<h6 className="g2t isbt"></h6>
														<h2 style={{fontSize:'0.6rem',lineHeight:'0.9rem',color:'grey'}}>
																 
														</h2>
												</Col>
												<Col md={4} xs={12} className="isbiw">
														<Card><div className="isbi isbi2" /></Card>
														<h6 className="g2t isbt"></h6>
														<h2 style={{fontSize:'0.6rem',lineHeight:'0.9rem',color:'grey'}}>
																 -  DApp</h2>	
												</Col>
										</Row>
								</section></LazyLoad>
								<LazyLoad height={200}><section className="is4">
										<h1></h1>
										<h6></h6>
										<Row className="grid">
												<Col md={3} xs={6} className="g4c">
														<Row className="g4i i41" />
														<Row><Col className="g4t"></Col></Row>
														<Row className="g4t"><Col>4TNAS</Col></Row>
												</Col>
												<Col md={3} xs={6} className="g4c">
														<Row className="g4i i42" />
														<Row><Col className="g4t"></Col></Row>
														<Row className="g4t"><Col>2.3GHz Intel </Col></Row>
												</Col>
												<Col md={3} xs={6} className="g4c">
														<Row className="g4i i43" />
														<Row><Col className="g4t"></Col></Row>
														<Row className="g4t"><Col> PCIE</Col></Row>
												</Col>
												<Col md={3} xs={6} className="g4c">
														<Row className="g4i i44" />
														<Row><Col className="g4t"></Col></Row>
														<Row className="g4t"><Col>30W</Col></Row>														
												</Col>
												<text className="tp">
														*265*200*180mm, 4G</text>
										</Row>
								</section></LazyLoad>
								<LazyLoad height={200}><section className="is5">
										<h1></h1>
										<h6></h6>
										<MButton
												onClick={()=>{
																history.push('/api/invite/new')
																history.go(0) }}
												style={{width:'8rem',marginTop:'1.5rem'}}
												variant="raised" color="secondary" ></MButton>
										<div className="i51" />
								</section></LazyLoad>
						</div>
				)
		}
}

export default IndexPage


